.main-img {
  position: relative;
  height:505px;
  width:705px;
  z-index: 10;
  overflow: hidden;

  img {
    width:705px;
    height:505px;
  }

  &:after {
    content: "";
    position: absolute;
    top:0;
    left:0;
    width:620px;
    height:515px;
    background-image: url("../img/microscheme-bg__desk.png");
    background-image: image-set(
      url("../img/microscheme-bg__desk.webp") type("image/webp"),
      url("../img/microscheme-bg__desk.png") type("image/jpeg"));
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/microscheme-bg__desk@2x.png");
      background-image: image-set(
        url("../img/microscheme-bg__desk@2x.webp") type("image/webp"),
        url("../img/microscheme-bg__desk@2x.png") type("image/jpeg"));
    }
  }

  @media (max-width: $desktop-width) {
    height:505px;
    width:658px;

    img {
      height:471px;
      width:658px;
    }

    &:after {
      width:620px;
      height:515px;
    }
  }

  @media (max-width: $desktop-medium-width) {
    height:350px;
    width:440px;

    img {
      height:287px;
      width:403px;
    }

    &:after {
      width:420px;
      height:349px;
    }
  }

  @media (max-width: $tablet-width) {
    position: absolute;
    top:82px;
    right:0;
    width:305px;
    height:405px;
    box-sizing: border-box;
    padding:30px 0 0 50px;

    img {
      width:465px;
      height:340px;
    }

    &:after {
      width:440px;
      height:403px;
      top:-5px;
      left:-13px;
    }
  }

  @media (max-width:$mobile-width-only) {
    position: absolute;
    top:62px;
    left:calc(50% - 130px);
    right:0;
    width:auto;
    height:520px;
    z-index: 1;
    padding:30px 0 0 0px;

    img {
      width:301px;
      height:220px;
    }

    &:after {
      width:403px;
      height:390px;
      top:0;
      left:133px;
    }
  }
}
