.form {
  margin:0 22px 0 0;

  &.form--modal {
    margin:0;
  }

  @media (max-width: $tablet-width) {
    margin:0;
  }

  p {
    font-family: "PT Mono", "Arial", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #fff;
    margin:8px 0 0 0;

    @media (max-width: $desktop-medium-width) {
      width:100%;
    }

    @media (max-width: $tablet-width) {
      width:100%;
      margin:15px 0 0 2px;
    }


    @media (max-width: $mobile-width-only) {
      width:100%;
      margin:12px 0 0 0;
    }
  }
}

.form__name {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
  color: #fff;
  margin:0;

  @media (max-width: $desktop-medium-width) {
    width:100%;
    font-size: 27px;
    line-height: 51px;
  }

  @media (max-width: $tablet-width) {
    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: $mobile-width-only) {
    width:100%;
    font-size: 28px;
    line-height: 39px;
  }
}

.form__form {
  width:570px;
  margin:49px 0 0 0;

  textarea {
    width:100%;
    height:154px;
    margin:40px 0 0 0;
    padding:13px 0 0 17px;
    border:solid 1px $main;
    box-sizing: border-box;
    outline:none;
    background-color: rgba(255,255,255,0.1);
    font-family: "PT Mono", "Arial", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height:22px;
    color: #fff;

    @media (max-width: $desktop-medium-width) {
      margin:25px 0 0 0;
    }

    @media (max-width: $tablet-width) {
      padding:9px 0 0 17px;
      margin:44px 0 0 0;
    }

    @media (max-width: $mobile-width-only) {
      margin:30px 0 0 0;
      padding:13px 0 0 17px;
    }

    &:hover {
      border:solid 1px #fff;
    }

    &:focus {
      border:solid 1px #fff;
    }

    &:active {
      border:solid 1px #fff;
    }

    &::placeholder {
      font-family: "PT Mono", "Arial", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height:22px;
      color: #fff;
    }
  }

  @media (max-width: $desktop-medium-width) {
    width:430px;
    margin:30px 0 0 0;
  }

  @media (max-width: $tablet-width) {
    width:590px;
    margin:51px 0 0 0;

    textarea {
      height:159px;
    }
  }

  @media (max-width: $mobile-width-only) {
    width:100%;
    margin:25px 0 0 0;

    textarea {
      height:104px;
    }
  }
}

.form--modal {
  position: absolute;
  display: block;
  top:136px;
  left:calc(50% - 245px);
  width:490px;
  height:605px;
  background-color: $main;
  padding:33px 39px 0 39px;
  box-sizing: border-box;
  z-index: 25;

  &.form--close {
    display: none;
  }

  .form__close {
    position: absolute;
    top:20px;
    right:20px;
    background: transparent;
    width:23px;
    height:23px;

    &:hover {
      cursor: pointer;
    }

      &::before {
      content: "";
      position: absolute;
      top: 10px;
      left: 0px;
      width:23px;
      height: 1px;
      background: #fff;
      transform: rotate(45deg);
      box-shadow: none;
      }

      &::after {
      content: "";
      position: absolute;
      top: 10px;
      left: 0px;
      width: 23px;
      height: 1px;
      background: #fff;
      transform: rotate(-45deg);
    }
  }

  .form__name {
    font-size: 28px;
    line-height: 39px;
  }

  .form__text {
    width:400px;
    font-size: 16px;
    line-height: 22px;
  }
}

.form__form--modal {
  width:412px;
  margin:35px 0 0 0;

  textarea {
    height:104px;
    margin:30px 0 0 0;
    padding:14px 0 0 24px;
  }
}
