.sd--desk {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    font-family: "PT Mono", "Arial", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #1B2D37;
    opacity: 0.5;
    margin:5px 0 0 15px;
  }

  @media (max-width: $mobile-width-only) {
    margin:24px 0 0 0;
  }
}
